'use client';
import { useState } from 'react';
import styles from './Sidebar.module.css';

const FilterItem = ({ title, options, defaultOpen = false }) => {
  const [isOpen, setIsOpen] = useState(defaultOpen);
  
  return (
    <div className={styles.filterItem}>
      <div className={styles.filterHeader} onClick={() => setIsOpen(!isOpen)}>
        <span className={styles.filterTitle}>{title}</span>
        <span className={styles.arrow}>{isOpen ? '‚ñ≤' : '‚ñº'}</span>
      </div>
      <div className={styles.filterSub}>All</div>
      {isOpen && (
        <div className={styles.filterOptions}>
          <button className={styles.unselectBtn}>Unselect all</button>
          {options.map((option, index) => (
            <label key={index} className={styles.optionLabel}>
              <input type="checkbox" />
              <span>{option}</span>
            </label>
          ))}
        </div>
      )}
    </div>
  );
};

const Sidebar = () => {
  return (
    <aside className={styles.sidebar}>
      <div className={styles.customizable}>
        <label className={styles.checkboxLabel}>
          <input type="checkbox" />
          <span>CUSTOMIZABLE</span>
        </label>
      </div>
      
      <FilterItem title="IDEAL FOR" options={['Men', 'Women', 'Baby & Kids']} />
      <FilterItem title="OCCASION" options={['Casual', 'Formal', 'Party']} />
      <FilterItem title="WORK" options={['Office', 'Field', 'Remote']} />
      <FilterItem title="FABRIC" options={['Cotton', 'Linen', 'Wool']} />
      <FilterItem title="SEGMENT" options={['Premium', 'Regular', 'Budget']} />
      <FilterItem title="SUITABLE FOR" options={['Summer', 'Winter', 'All season']} />
      <FilterItem title="RAW MATERIALS" options={['Organic', 'Recycled', 'Synthetic']} />
      <FilterItem title="PATTERN" options={['Plain', 'Striped', 'Checkered']} />
    </aside>
  );
};

export default Sidebar;
